@use 'base/variables' as v;
@use 'base/mixins' as m;

.reset{
    margin-top: 5rem;
    .uptask{
       @include m.textoDegradado(v.$cobalto, v.$cobalto);
    }
    input[type="submit"]{
        @include m.boton(v.$cobalto, v.$blanco);
        @include m.tablet{
            margin: 0 0 0 67%
        }
    }
    .acciones{
        a{
            color: v.$cobalto;
        }
    }
}