@use 'base/variables' as v;
@use 'base/mixins' as m;

.login{
    margin-top: 7rem;
    .uptask{
       @include m.textoDegradado(v.$lapislazuli, v.$lapislazuli);
    }
    input[type="submit"]{
        @include m.boton(v.$lapislazuli, v.$blanco);
        @include m.tablet{
            margin: 0 0 0 73%
        }
    }
    .acciones{
        a{
            color: v.$lapislazuli;
        }
    }
} 