@use 'base/mixins' as m;
@use 'base/variables' as v;

.alerta{
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    text-align: center;
    font-weight: 900;
    margin: 1.5rem 0;
    font-size: 1.4rem;
    max-width: 80rem;
    border-top-right-radius: 1rem;
    border-bottom-left-radius: 1rem;
    &.error{
        background-color: lighten(v.$rojo, 40%);
        border-left: 0.5rem solid v.$rojo;
        border-right: 0.5rem solid v.$rojo;
    }
    &.exito{
        background-color: lighten(v.$verde, 50%);
        border-left: 0.5rem solid v.$verde;
        border-right: 0.5rem solid v.$verde;
    }
    &.mensaje{
        background-color: lighten(v.$imperial, 40%);
        border-left: 0.5rem solid v.$imperial;
        border-right: 0.5rem solid v.$imperial;
    }
}