@use 'base/mixins' as m;
@use 'base/variables' as v;

.modal{
    position: absolute;
    background-color: rgba(v.$negro, 50%);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    .formulario{
        max-width: 60rem;
        background-color: v.$blanco;
        border-radius: 1rem;
        padding: 3rem;
        margin: 0 auto;
        transform: translateY(-100%);
        transition-property: all;
        transition-duration: 1000ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar{
            transform: translateY(-200%);
            opacity: 0;
        }
        legend{
            margin-bottom: 2rem;
            text-align: center;
            font-weight: bold;
            display: block;
            font-size: 2rem;
        }
        .campo{
            margin: 2rem 0 3rem 0;
        }
        input:not([type="submit"]){
            background-color: lighten(v.$gris, 40%);
        }
        .opciones{
            display: flex;
            justify-content: space-between;
        }
        .submit-nueva-tarea{
            @include m.boton(v.$navy, v.$blanco);
            padding: 1rem 2rem;
        }
        .cerrar-modal{
            @include m.boton(v.$rojo, v.$blanco);
            padding: 1rem 2rem;
        }
    }
}