@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-nueva-tarea{
    display: flex;
    justify-content: center;
    .agregar-tarea{
        @include m.boton(v.$lapislazuli, v.$blanco);
        padding: 1rem 2rem;
    }
}
.listado-tareas{
    list-style: none;
    padding: 0;
    margin-top: 5rem;
    .no-tareas{
        text-align: center;
        font-size: 3rem;
        font-weight: 700;
        color: v.$rojo;
    }
}
.tarea{
    padding: 0.5rem 2rem;
    border-bottom: 1px solid lighten(v.$navy, 60%);
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:last-of-type{
        border-bottom: none;
    }
    p{
        font-size: 1.5rem;
        cursor: pointer;
    }
    .opciones{
        display: flex;
        gap: 0.5rem;
        button{
            border: none;
            font-size: 1.4rem;
            padding: 0.5rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: 1rem;
            &:hover{
                cursor: pointer;
            }
        }
        .completa{
            background-color: v.$navy;
            color: v.$blanco;
        }
        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
        }

        .eliminar-tarea{
            background-color: v.$rojo;
            color: v.$blanco;
        }
    }
}