@use 'base/variables' as v;
@use 'base/mixins' as m;

.olvido{
    margin-top: 5rem;
    .uptask{
       @include m.textoDegradado(v.$imperial, v.$imperial);
    }
    input[type="submit"]{
        @include m.boton(v.$imperial, v.$blanco);
        @include m.tablet{
            margin: 0 0 0 67%
        }
    }
    .acciones{
        a{
            color: v.$imperial;
        }
    }
}