@use 'base/mixins' as m;
@use 'base/variables' as v;
.no-proyectos{
    text-align: center;
    a{
        color: v.$navy;
    }
}
.listado-proyectos{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    border-radius: 1rem;
    @include m.grid(1, 2rem);
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    @include m.desktop{
        @include m.grid(3, 2rem);
    }
    .proyecto{
        border-radius: 1rem;
        background-color: lighten(v.$cobalto, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        line-height: 2;
        text-align: center;
        &:hover{
            background-color: darken(v.$cobalto, 5%);
        }
        a{
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 2.5rem;
            height: 100%;
            width: 100%;
            color: v.$blanco;
        }
    }
}