@use 'base/mixins' as m;
@use 'base/variables' as v;

.sidebar{
    //display: none;
    opacity: 0;
    background-color: v.$navy; //linear-gradient( lighten(v.$navy, 20%), darken(v.$nocturno, 5%) 100%);
    transition: all;
    transition-duration: 200ms;
    height: 0;
    overflow: hidden;
    &.mostrar{
        //padding: 2rem;
        height: auto;
        opacity: 1;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgb(0 0 0 / .90);
        /*@include m.tablet{
            opacity: 1;
            background: linear-gradient( lighten(v.$navy, 20%), darken(v.$nocturno, 5%) 100%);
            transition: all;
            transition-duration: 200ms;
            position: relative;
            height: auto;
            overflow: hidden;
        }*/
    }

    &.ocultar{
        opacity: 0;
        @include m.tablet{
            opacity: 1;
        }
    }
    @include m.tablet{
        //background: linear-gradient( lighten(v.$navy, 20%), darken(v.$nocturno, 5%) 100%);
        padding: 2rem;
        height: auto;
        opacity: 1;
        display: block;
    }
    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2rem 3.8rem;
        @include m.tablet{
            display: block;
            padding: 0;
        }
        h2{
            font-size: 4rem;
            color: v.$blanco;
            margin: 0;
            text-align: center;
            @include m.tablet{
                font-size: 6rem;
            }
        }
        .cerrar-menu{
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;
            img{
                &:hover{
                    cursor: pointer;
                }
            }
            @include m.tablet{
                display: none;
            }
        }
    }
    .sidebar-nav{
        margin-top: 5rem;
        a{
            @include m.tablet{
                font-size: 1.7rem;
                margin-bottom: 1.5rem;
            }
            color: v.$blanco;
            display: block;
            font-size: 3rem;
            padding: 0.7rem;
            margin-bottom: 3rem;
            text-align: center;
            text-transform: uppercase;
            font-weight: 450;
            transition-property: background-color;
            transition-duration: 300ms;
            &:hover, 
            &.activo{
                background-color: darken(v.$lapislazuli, 30%);
                border-radius: 1rem;
            }
        }
    }
    .cerrar-sesion-mobile{
        margin-top: 10rem;
        @include m.tablet{
            display: none;
        }
        .cerrar-sesion{
            @include m.boton(v.$navy, v.$blanco);
            margin: 2rem;
            width: 90%;
            font-size: 3rem;
            //padding: 2rem 3rem;
        }
    }
}