@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard{
    background-color: v.$grisClaro;
    @include m.tablet{
        display: flex;
        height: 100vh; 
    }
    .principal{
        flex: 1;
        overflow: scroll;
    }
    .contenido{
        padding: 2rem;
        .nombre-pagina{
            font-size: 2.8rem;
            text-align: center;
            text-transform: capitalize;
        }
    }
    .formulario{
        input:not([type = "submit"]){
            background-color: v.$blanco;
        }
        input[type="submit"]{
            @include m.boton(v.$navy, v.$blanco);
            text-transform: uppercase;
            padding: 1rem 2rem;
            font-weight: bold;
        }
    }
    .enlace{
        display: block;
        margin-top: 3rem;
        text-align: right;
        width: 100%;
        &:hover{
            color: darken(v.$navy, 20%);
            cursor: pointer;
        }
    }
}