@use 'base/mixins' as m;
@use 'base/variables' as v;

.barra-mobile{
    background-color: v.$navy;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet{
        display: none;
    }
    h1{
        font-size: 4rem;
        color: v.$blanco;
        margin: 0;
    }
    .menu{
        img{
            width: 0.8rem;
            &:hover{
                cursor: pointer;
            }
        }
    }
}
.barra{
    display: none;
    @include m.tablet{
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px v.$negroDegradado;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    p{
        margin: 0;
        font-size: 1.4rem;
        span{
            font-weight: 700;
            color: v.$navy;
        }
    }
    .cerrar-sesion{
        @include m.boton(darken(v.$navy, 5%), v.$blanco);
        font-size: 1.4rem;
        padding: 1rem 2rem;
    }
}