@use 'base/variables' as v;
@use 'base/mixins' as m;

@media (prefers-color-scheme: dark){
    $grisOscuro: #2c2c3b;
    body{
        background-color: $grisOscuro;
    }
    .tagline,
    label, 
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvido .acciones a,
    .reset .acciones a,
    .alerta.error{
        color: v.$blanco;
    }
    .barra {
        p {
            color: v.$blanco;
            span{
                color: v.$pasto;
            }
        }
        .cerrar-sesion{
            background-color: v.$pasto;
        }
    }
    .descripcion-pagina, 
    .formulario .campo input, 
    .nombre-pagina{
        color: v.$grisClaro;
    }
    
    .formulario .campo input,
    .dashboard .formulario input:not([type="submit"]){
        background-color: lighten(v.$grisOscuro, 15%);
    }
    .alerta.error{
        background-color: v.$rojo;
        border-left: 0.5rem solid darken(v.$rojo, 50%);
        border-right: 0.5rem solid darken(v.$rojo, 50%);
    }
    .sidebar{
        @include m.tablet{
            background: darken($grisOscuro, 10%);
        }
        .sidebar-nav a{
            &.activo{
                background-color: lighten(v.$navy, 10%);
            }
            &:hover{
                background-color: lighten(v.$pasto, 5%);
            }
        }
    }
    .modal .formulario{
        background-color: $grisOscuro;
        legend{
            color: v.$grisClaro;
        }
        input[type="text"]{
            background-color: lighten(v.$grisOscuro, 10%);
            color: v.$blanco;
            &::placeholder{
                color: darken(v.$grisClaro, 5%);
            }
        }
    }
    .barra-mobile{
        background-color: darken($grisOscuro, 10%);
    }
    .filtros,
    .barra{
        background-color: lighten($grisOscuro, 10%);
    }
    .filtros{
        h2,
        label{
            color: v.$blanco;
        }
        input[type="radio"]{
            accent-color: lighten(v.$navy, 60%);
        }
    }

    .dashboard .enlace{
        color: v.$blanco;
        &:hover{
            color: darken(v.$grisClaro, 20%);
        }
    }
    .dashboard .contenido,
    .dashboard .principal{
        background-color: $grisOscuro;
    }
    .listado-proyectos .proyecto{
        background-color: lighten($grisOscuro, 10%);
        &:hover{
            background-color: darken($grisOscuro, 10%);
        }
    }
    .listado-tareas .tarea{
        background-color: lighten($grisOscuro, 10%);
        border-radius: 1rem;
        border: none;
        p{
            color: v.$grisClaro;
        }
    }
}