@use 'base/variables' as v;
@use 'base/mixins' as m;

.confirmacion{
    margin-top: 5rem;
    .uptask{
       @include m.textoDegradado(v.$navy, v.$navy);
    }
    input[type="submit"]{
        @include m.boton(v.$navy, v.$blanco);
        @include m.tablet{
            margin: 0 0 0 67%
        }
    }
    .acciones{
        justify-content: center;
        .boton{
            @include m.boton(v.$navy, v.$blanco);
            border-radius: 1rem;
        }
    }
}