html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario label {
  flex: 0 0 10rem;
}
.formulario input:not([type=submit]) {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  background-color: #bdc0c7;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  margin-bottom: 2rem;
  display: block;
  text-align: center;
  color: #000;
}
.acciones a:hover {
  cursor: pointer;
}
.acciones a:last-of-type {
  margin: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 900;
  margin: 1.5rem 0;
  font-size: 1.4rem;
  max-width: 80rem;
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
}
.alerta.error {
  background-color: #ff9393;
  border-left: 0.5rem solid #c60000;
  border-right: 0.5rem solid #c60000;
}
.alerta.exito {
  background-color: #78ff78;
  border-left: 0.5rem solid #007800;
  border-right: 0.5rem solid #007800;
}
.alerta.mensaje {
  background-color: #81b8f3;
  border-left: 0.5rem solid #0f5199;
  border-right: 0.5rem solid #0f5199;
}

.login {
  margin-top: 7rem;
}
.login .uptask {
  background: linear-gradient(to right, #98aac4, #50698d 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #59759d;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #475d7c;
  cursor: pointer;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    margin: 0 0 0 73%;
  }
}
.login .acciones a {
  color: #59759d;
}

