.sidebar {
  opacity: 0;
  background-color: #000080;
  transition: all;
  transition-duration: 200ms;
  height: 0;
  overflow: hidden;
}
.sidebar.mostrar {
  height: auto;
  opacity: 1;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.9);
  /*@include m.tablet{
      opacity: 1;
      background: linear-gradient( lighten(v.$navy, 20%), darken(v.$nocturno, 5%) 100%);
      transition: all;
      transition-duration: 200ms;
      position: relative;
      height: auto;
      overflow: hidden;
  }*/
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar.ocultar {
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .sidebar {
    padding: 2rem;
    height: auto;
    opacity: 1;
    display: block;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
